'use client'

import React, { useState, ChangeEvent, FormEvent } from "react";

interface ContactData {
  name: string;
  lastName: string;
  email: string;
  phone: string;
  enterprise: string;
  creationDate: string; // Cambio a string para mostrar solo la fecha
  lastInteraction: string; // Cambio a string para mostrar solo la fecha
}

interface EnterpriseData {
  name: string;
  description: string;
  industry: string;
  webpageUrl: string;
  creationDate: string; // Cambio a string para mostrar solo la fecha
}

export default function NewContactPage() {
  const [formType, setFormType] = useState<string>("New Contact");
  
  // Formulario de Contacto
  const [contactData, setContactData] = useState<ContactData>({
    name: "",
    lastName: "",
    enterprise: "",
    phone: "",
    email: "",
    creationDate: new Date().toLocaleDateString("en-US"), // Solo la fecha
    lastInteraction: new Date().toLocaleDateString("en-US"), // Solo la fecha
  });

  // Formulario de Empresa
  const [enterpriseData, setEnterpriseData] = useState<EnterpriseData>({
    name: "",
    description: "",
    industry: "",
    webpageUrl: "",
    creationDate: new Date().toLocaleDateString("en-US"), // Solo la fecha
  });

  const enterprises = ["Enterprise 1", "Enterprise 2", "Enterprise 3"];

  // Funciones de cambio para contacto
  const handleChangeContact = (
    e: ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>
  ): void => {
    setContactData({ ...contactData, [e.target.name]: e.target.value });
  };

  // Funciones de cambio para empresa
  const handleChangeEnterprise = (
    e: ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>
  ): void => {
    setEnterpriseData({ ...enterpriseData, [e.target.name]: e.target.value });
  };

  // Enviar datos de contacto
  const handleSubmitContact = (e: FormEvent): void => {
    e.preventDefault();
    console.log("Contact Data Submitted", contactData);
  };

  // Enviar datos de empresa
  const handleSubmitEnterprise = (e: FormEvent): void => {
    e.preventDefault();
    console.log("Enterprise Data Submitted", enterpriseData);
  };

  return (
    <div>
      <h1>{formType} Form</h1>
      <select onChange={(e) => setFormType(e.target.value)} value={formType}>
        <option>New Contact</option>
        <option>New Enterprise</option>
      </select>

      {formType === "New Contact" ? (
        <form onSubmit={handleSubmitContact}>
          <div>
            <label>Name:</label>
            <input
              type="text"
              name="name"
              value={contactData.name}
              onChange={handleChangeContact}
              required
            />
          </div>
          <div>
            <label>Last Name:</label>
            <input
              type="text"
              name="lastName"
              value={contactData.lastName}
              onChange={handleChangeContact}
              required
            />
          </div>
          <div>
            <label>Enterprise:</label>
            <select
              name="enterprise"
              value={contactData.enterprise}
              onChange={handleChangeContact}
              required
            >
              <option value="">Select Enterprise</option>
              {enterprises.map((enterprise, index) => (
                <option key={index} value={enterprise}>
                  {enterprise}
                </option>
              ))}
            </select>
          </div>
          <div>
            <label>Phone:</label>
            <input
              type="tel"
              name="phone"
              value={contactData.phone}
              onChange={handleChangeContact}
            />
          </div>
          <div>
            <label>Email:</label>
            <input
              type="email"
              name="email"
              value={contactData.email}
              onChange={handleChangeContact}
            />
          </div>
          <div>
            <label>Creation Date:</label>
            <input
              type="text"
              name="creationDate"
              value={contactData.creationDate}
              disabled
            />
          </div>
          <div>
            <label>Last Interaction:</label>
            <input
              type="text"
              name="lastInteraction"
              value={contactData.lastInteraction}
              disabled
            />
          </div>
          <button type="submit">Submit</button>
        </form>
      ) : (
        <form onSubmit={handleSubmitEnterprise}>
          <div>
            <label>Name:</label>
            <input
              type="text"
              name="name"
              value={enterpriseData.name}
              onChange={handleChangeEnterprise}
              required
            />
          </div>
          <div>
            <label>Description:</label>
            <textarea
              name="description"
              value={enterpriseData.description}
              onChange={handleChangeEnterprise}
            />
          </div>
          <div>
            <label>Industry:</label>
            <input
              type="text"
              name="industry"
              value={enterpriseData.industry}
              onChange={handleChangeEnterprise}
              required
            />
          </div>
          <div>
            <label>Webpage URL:</label>
            <input
              type="url"
              name="webpageUrl"
              value={enterpriseData.webpageUrl}
              onChange={handleChangeEnterprise}
            />
          </div>
          <div>
            <label>Creation Date:</label>
            <input
              type="text"
              name="creationDate"
              value={enterpriseData.creationDate}
              disabled
            />
          </div>
          <button type="submit">Submit</button>
        </form>
      )}
    </div>
  );
}